.App {
  padding: 30px;
  display: flex;
  flex-wrap: wrap;
}

.App > div {
  margin-right: 50px;
}

.linePreloader{
  width: 1200px;
  height:6px;
  background:linear-gradient(to right,green,green);
  background-color:#ccc;
  position: relative;
  top:0;
  bottom:0;
  left:0;
  right:0;
/*  margin:auto;*/
  margin-bottom: 30px;
  border-radius:4px;
  background-size:20%;
  background-repeat:repeat-y;
  background-position:-25% 0;
  animation:scroll 1.2s ease-in-out infinite;
}

@keyframes scroll{
  50%{background-size:80%}
  100%{background-position:125% 0;}
}


